      <div class="p-4">
        <div class="input-gray p-4">
          <textarea v-model="mile_content" class="lg2" placeholder="ÏÉÅÏÑ∏ ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî" style="text-align: left; width: 100%; height: 30vh"></textarea>
        </div>
