<script>
import { mapActions, mapGetters } from 'vuex';
export default {
  name: 'IntroduceMileageAddAdminView',
  computed: {
    ...mapGetters('login', ['getLoginInfo', 'getIsChecked']),
    ...mapGetters('mile', ['getMileInfo']),
    loginInfo(){
      return this.getLoginInfo;
    },
    mileInfo() {
      return this.getMileInfo;
    },
    isChecked() {
      return this.getIsChecked;
    }
  },
  data(){
    return{
      mile_title: '',
      mile_content: '',
      mile_no: '',
      mile_route: '',
      file: null,
      selectedJob: 'Í≥µÌÜµ' // Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï 
    }
  },
  methods: {
    ...mapActions('mile', ['getMileDetail', 'addMile']),
    goBack() {
      this.$router.go(-1);
    },

    handlerFileUpload(event){
      this.file = event.target.files[0]; // ÏÑ†ÌÉùÎêú ÌååÏùº Í∞ùÏ≤¥Î•º dataÏùò fileÏóê ÏÑ†Ïñ∏.
    },
    
    async addAction(){
      const mileInfo = new FormData(); // FormDataÌòïÏãùÏùÑ Ïì∞ÏßÄ ÏïäÏúºÎ©¥ JSON ÌòïÏãùÏúºÎ°ú ÌååÏùºÏùò Ïù¥Î¶ÑÎßå Ï†ÑÎã¨ÎêúÎã§. Ïã§Ï†ú ÌååÏùº Îç∞Ïù¥ÌÑ∞ Ï†ÑÏÜ°ÌïòÍ∏∞ ÏúÑÌï¥ FormData ÏÇ¨Ïö©
      mileInfo.append('mile_no', this.loginInfo ? this.loginInfo.mile_no : null);
      mileInfo.append('mile_title', this.mile_title);
      mileInfo.append('mile_content', this.mile_content);
      mileInfo.append('file', this.file || ''); // ÌååÏùºÏù¥ ÏóÜÏùÑ ÎïåÎèÑ file ÌïÑÎìúÎ•º Ï∂îÍ∞ÄÌïúÎã§.
      mileInfo.append('job_name', this.selectedJob); 
      
      const response = await this.addMile(mileInfo);

      if(response && response.data.success){
        this.showAlert('ÎßàÏùºÎ¶¨ÏßÄÍ∞Ä Îì±Î°ùÎêòÏóàÏäµÎãàÎã§', 'success', '/IntroduceMileageAdminView');
      }else{
        this.showAlert('ÎßàÏùºÎ¶¨ÏßÄ Îì±Î°ùÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§', 'fail', '#');
      }
    },
    
    showAlert(t, i, r) {
      this.$swal({
        title: t,
        icon: i,
        scrollbarPadding: false
      }).then((result) => {
        if(result.isConfirmed && r !== '#'){
          this.$router.push(r);
        }
      })
    },
    checkLoginInfo() {
      if ( !this.getLoginInfo ||
            this.getIsChecked === false ||
            this.getLoginInfo.user_is_manager === false) {
          window.location.href="/noAccess"
        }  
    },
  },
  created(){
    // ÎßàÏùºÎ¶¨ÏßÄ Îã¥ÎãπÏûê Î°úÍ∑∏Ïù∏ Ïó¨Î∂Ä ÌôïÏù∏
    this.checkLoginInfo();
    const mile_no = this.loginInfo ? this.loginInfo.mile_no : null;
    if(mile_no){
      this.getMileDetail(mile_no);
    }else{
      console.error('mile_noÏù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.');
    }
  }
};
</script>

